ContentSection = function({title, list = false, children}) {
  var id, inner;
  id = title.toLowerCase().replace("/", "");
  id = id.replace(" ", "-");
  inner = list ? <ul>
    {children.map(function(child, i) {
    return <li key={i}>
        {child}
      </li>;
  })}
    </ul> : children;
  return <Fragment>
    <h1 id={id}>{title}</h1>
    {inner}
    <hr />
  </Fragment>;
};



