// Generated by CoffeeScript 2.3.0
var Consent,
  boundMethodCheck = function(instance, Constructor) { if (!(instance instanceof Constructor)) { throw new Error('Bound instance method accessed before binding'); } };

import React, {
  Component
} from "react";

import "./Consent.css";

export default Consent = class Consent extends Component {
  constructor() {
    super(...arguments);
    this.handleButtonClick = this.handleButtonClick.bind(this);
    this.unCollapse = this.unCollapse.bind(this);
    this.state = {
      agreed: "true" === localStorage.GDPRConsent
    };
    this.state.collapsed = this.state.agreed;
  }

  handleButtonClick() {
    boundMethodCheck(this, Consent);
    return this.setState(function(state) {
      return {
        agreed: localStorage.GDPRConsent = !state.agreed,
        collapsed: true
      };
    });
  }

  unCollapse() {
    boundMethodCheck(this, Consent);
    return this.setState({
      collapsed: false
    });
  }

  render() {
    var agreed, collapsed;
    ({agreed, collapsed} = this.state);
    if (collapsed) {
      return <div className="consent collapsed" onClick={this.unCollapse} />;
    }
    return <div className="consent">
      This site uses Cookies.
      <button type="button" onClick={this.handleButtonClick}>
        {(agreed ? "disagree" : "agree")}
      </button>
    </div>;
  }

};



